import NO from '@/components/_nodata'
export default {
    data() {
        return {
            
        }
    },
    components:{
        'no-data': NO
    },
    beforeCreate(){
        this.$store.dispatch('entrust')
    },
    computed: {
        entrust(){
            return this.$store.state.entrust
        },
    },
    created(){
        document.title = '商铺签约'
    },
    methods: {
        sign(state) {
            state != -1 || state == 2 ? this.$router.push({path:'/Contract'}) : ''
        },
        num(){
            return Math.floor(Math.random()*5)
        }
    },
}



